body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: #212121;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  color: white;
}

#root {
  width: 100%;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 {
  font-weight: 300;
  font-size: 60px;
  line-height: 70px;
  letter-spacing: -0.5px;
}

.table {
  background-color: white;
  border-radius: 10px;
  height: auto;
  overflow: hidden;
  margin-top: 3vmax;
}

.table-header {
  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 17px;
  /* identical to box height */

  letter-spacing: 0.1em;
  text-transform: uppercase;

  color: #000000;

  position: relative;
  top: 1em;
  padding: 0 4vmin;
  height: 3em;
}

.table-header-total {
  position: absolute;
  right: 4vmin;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;

  /* Fonts/Primary */

  color: #25213B;
  text-transform: capitalize;
}

.table-header-total span {
  font-weight: bold;
  letter-spacing: 0.005em;
  color: rgb(183, 138, 87);
}