.container {
  width: 100%;
}

.row span {
  text-align: center;
  padding-top: 0.9em;
  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;

  color: #25213B;
}

.row {
  display: grid;
  grid-template-columns: 2fr 2fr 1fr 1fr 1fr;
  height: 3em;
}

.row:nth-child(1) {
  grid-column: 1 / span 1;
}
.row:nth-child(2) {
  grid-column: 2 / span 1;
}
.row:nth-child(3) {
  grid-column: 3 / span 1;
}
.row:nth-child(4) {
  grid-column: 4 / span 1;
}
.row:nth-child(5) {
  grid-column: 5 / span 1;
}

.header {
  background: #F4F2FF;
  height: 2em;
}

.header span {
  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 15px;
  /* identical to box height */

  letter-spacing: 0.05em;
  text-transform: uppercase;

  /* Fonts/Primary Variant */

  color: #6E6893;
}

.empty {
  background-color: white;
  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 18px;
  line-height: 24px;
  /* identical to box height */

  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: lightgray;

  text-align: center;
  padding: 2vmax 2vmax;
}