.container {
  margin-top: 2vmax;
  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 17px;
  /* identical to box height */

  letter-spacing: 0.1em;
  text-transform: uppercase;

  color: #909368;
}

.form {
  margin-top: 1vmin;
  /* margin-bottom: 3vmax; */
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}

.formGroup {
  padding: 6px;
  border-radius: 6px;
  background-color: white;
}

.input {
  border: none;
}

input:focus {
  outline: none;
}

select:focus {
  outline: none;
}

.dropdown {
  appearance: none;
  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 15px;
  color: #4A4AFF;

  border: none;
  width: 10vmin;
  margin: 0 4vmin;
  
  background: #E6E6F2;
  border-radius: 10px;

  text-align-last: center;
}

.submit {
  margin-left: 2vmin;
  background: #6D5BD0;
  border-radius: 6px;

  text-transform: uppercase;
  color: white;

  box-shadow: none;
  outline: none;
  border: 0;
  padding: 0 10px;
}